  display: none; }

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
/* Links */
a {
  color: #248a83; }
  a:hover, a:focus, a:active {
    color: #f0e4d1; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

.site-header {
  background-color: rgba(255, 255, 255, 0.85);
  border-bottom: 1px solid #c1c1c1;
  padding: 0.5rem 0;
  position: fixed;
  transition: top 0.5s linear;
  width: 100%;
  z-index: 3; }
  .site-header.reverse-header {
    background-color: transparent;
    border-bottom: 0;
    position: absolute;
    text-shadow: 1px 1px 2px #323232; }
    .site-header.reverse-header h1 {
      background: url(../../images/logos/inhabitent-logo-tent-white.svg) no-repeat;
      background-size: 50px 33px; }
    .site-header.reverse-header a {
      color: #FFF;
      text-transform: uppercase; }
      .site-header.reverse-header a:visited {
        color: #FFF; }
  .site-header .container {
    display: flex;
    justify-content: space-between; }
    .site-header .container .main-navigation {
      font-family: 'Novecento Sans Web';
      display: flex; }
      .site-header .container .main-navigation li {
        padding-right: 1.5rem; }
    .site-header .container .header-search fieldset {
      border: 0;
      padding: 0;
      margin: 0; }
      .site-header .container .header-search fieldset label {
        display: none; }
  .site-header h1 {
    background: url(../../images/logos/inhabitent-logo-tent.svg) no-repeat;
    background-size: 50px 33px;
    height: 33px;
    margin: 0;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    width: 50px; }
